import React, { Component, Fragment } from 'react';
import axios from 'axios';
import CardProduct from '../../../components/CardProduct/CardProduct';
import './ListProduct.scss';

class ListProduct extends Component {
  state = {
    post: []
  }

  numberSeparator = (x) => {
    return x.toString().replace(/\B(?=(\d{3})+(?!\d))/g, ".");
  }

  getDataToAPI = () => {
    axios.get('http://localhost/server-react-crud-1/view.php')
    .then(res => {
      this.setState({
        post: res.data
      }, () => {
        console.log(this.state.post);
      })
    })
  }

  componentDidMount() {
    this.getDataToAPI();
  }

  render() {
    return(
      <Fragment>
          <div className="list-product-cilent">
            {
              this.state.post.map(post => {
                return (
                  <CardProduct
                    key={post.id}
                    harga={this.numberSeparator(post.harga)}
                    hargaReal={this.numberSeparator(post.harga_real)}
                    img={`http://localhost/server-react-crud-1/files/${post.img}`}
                    post={post}
                  />
                )
              })
            }
          </div>
      </Fragment>
    )
  }
}

export default ListProduct;