import React, { Fragment } from 'react';
import { connect } from 'react-redux';
// import DummyImg from '../../assets/img1.jpg';
import ActionType from '../../redux/reducer/globalActionType';
import './CardProduct.scss';

const CardProduct = (props) => {
  return(
    <Fragment>
      <div className="card-product-con" onClick={ () => props.showDetail(props.post)}>
        <div className="card-product-img">
          <img src={props.img} alt="img"/>
          <div className="img-shades">
          </div>
        </div>
        <div className="card-product-body">
          <div className="body-name">
            Yamaha {props.post.nama}
          </div>
          <div className="body-detail">
          <div className="detail-untit">
              Stok : {props.post.stok} Unit
            </div>
            <div className="detail-sold">
              Terjual : {props.post.beli} Unit
            </div>
          </div>
          <div className="body-price">
            <div className="real-price">
              Rp. {props.hargaReal}
            </div>
            <small className="diskon-price">
              {props.post.diskon > 0 ? 
                `Rp. ${props.harga}` : null
              }
            </small>
          </div>
        </div>
      </div>
    </Fragment>
  )
}

const mapDispatchToProps = dispatch => {
  return {
    showDetail: (e) => dispatch({type: ActionType.SHOW_DETAIL_PRODUCT, data: e})
  }
}

export default connect(null, mapDispatchToProps) (CardProduct);