import axios from 'axios';
import ActionType from './globalActionType';

const globalState = {
  totalOrder: 0,
  toggleCart: false,
  showDetailProduct: false,
  detailPost: null,
  cart: null,
  user: {}, //id_user, nama_user, foto_user
  cart: []
}

// Reducer
const rootReducer = (state = globalState, action) => {
  switch (action.type) {
    case ActionType.GET_DATA_USER:
      return {
        ...state,
        user: action.data
      }
    case ActionType.SHOW_DETAIL_PRODUCT:
      return {
        ...state,
        showDetailProduct: true,
        detailPost: action.data
      }
    case ActionType.HIDE_DETAIL_PRODUCT:
      return {
        ...state,
        showDetailProduct: false
      }
    case ActionType.TOGGLE_CART:
      return {
        ...state,
        toggleCart: !state.toggleCart
      }
    case ActionType.GET_CART:
      return {
        ...state,
        cart: action.data
      }
      
  }
  return state;
}

export default rootReducer;