import React, { Component, Fragment } from 'react';
import { connect } from 'react-redux';
import DummyImg from '../../assets/img1.jpg'
import ActionType from '../../redux/reducer/globalActionType';
import './NavCilent.scss';

const NavCilent = (props) => {

  function countCartOrder() {
    let i = 0;
    if(props.cart) {
      props.cart.map(cart => {
        i++
      })
    }
    return i
  }
  
  return (
    <Fragment>
      <nav className="nav-cilent">
        <div className="nav-con">
          <div className="nav-logo">
            MusicShop
          </div>
          <div className="nav-item-con">
            <div className="nav-cart" onClick={props.toggleShowCart}>
              <i className="fa fa-shopping-cart"></i>
              <div className="cart-counter">
                {countCartOrder()}    
              </div>
            </div>
            <div className="nav-account">
              <div className="account-name">
                <i className="fa fa-circle" aria-hidden="true"></i>
                {props.user.nama_user}
              </div>
              <div className="account-img">
                {props.user.foto_user ? 
                <img src={`http://localhost/server-react-crud-1/user/${props.user.foto_user}`} alt="IMG"/> : <img src={DummyImg} alt="IMG"/>}
              </div>
            </div>
          </div>
        </div>
      </nav>
    </Fragment>
  )
}

const mapStateToProps = state => {
  return {
    cart: state.cart,
    user: state.user
  }
}

const mapDispatchToProps = dispatch => {
  return {
    toggleShowCart: () => dispatch({type: ActionType.TOGGLE_CART})
  }
}

export default connect(mapStateToProps, mapDispatchToProps)(NavCilent);