import React, { Fragment, useEffect, useState } from 'react';
import './DetailProduct.scss';
// import Dummy from '../../assets/img1.jpg';
import ActionType from '../../redux/reducer/globalActionType';
import { connect } from 'react-redux';
import Axios from 'axios';


const numberSeparator = (x) => {
  return x.toString().replace(/\B(?=(\d{3})+(?!\d))/g, ".");
}

const DetailProduct = (props) => {
  function getDataCartToAPI() {
    Axios.get(`http://localhost/server-react-crud-1/get/get-cart.php?id=${props.user.id_user}`)
    .then(res => {
      props.getDataCart(res.data)
    })
  }

  function postCartToAPI(){
    const cart = {
      id_user: props.user.id_user,
      id_produk: props.post.id
    }
    Axios.post(`http://localhost/server-react-crud-1/post/post-cart.php`, cart)
    .then(res => {
      getDataCartToAPI(props.user.id_user)
    })
  }

  function removeCartToAPI() {
    Axios.delete(`http://localhost/server-react-crud-1/delete/delete-cart.php?id=${idCart}`)
    .then(res => {
      getDataCartToAPI()
    })
  }

  const [isCart, setIsCart] = useState(false)
  const [idCart, setidCart] = useState(null)
  function isCartChecked() {
    setIsCart(false)
    if(props.cart) {
      props.cart.map(cart => {
        if(cart.id === props.post.id) {
          setidCart(cart.id_cart)
          setIsCart(true)
        }
      })
    }
  }
  
  useEffect(() => {
    isCartChecked()
  })

  return (
    <Fragment>
      <div className="detail-product-con">
        <div className="detail-product">
          <div className="detail-close-btn" onClick={props.hideDetail}>
            <i className="fa fa-times-circle" aria-hidden="true"></i>
          </div>
          <div className="detail-header">
            Detail Product
          </div>
          <div className="detail-body">
            <div className="body-img">
              <img src={`http://localhost/server-react-crud-1/files/${props.post.img}`} alt="img"/>
            </div>
            <div className="body-text">
              <div className="body-info">
                <div className="body-info-1">
                  <div className="body-name">
                    {props.post.nama}
                  </div>
                  <div className="body-category">
                    Accoustic
                  </div>
                </div>
                <div className="body-info-2">
                  <div className="body-price">
                    Rp. {numberSeparator(props.post.harga_real)}
                  </div>
                  <div className="body-discount">
                    {props.post.diskon > 0 ?
                      `Rp. ${numberSeparator(props.post.harga)}` : null
                    }
                  </div>
                </div>
              </div>
              <div className="body-desc">
                {props.post.deskripsi} Lorem ipsum dolor sit amet, consectetur adipisicing elit. Suscipit commodi voluptatum reprehenderit autem a neque perferendis magnam atque sed eaque veniam placeat et cupiditate, corrupti quas numquam quam velit quia!
              </div>
              <div className="body-submit">
                {isCart ?
                  <button className="submit-remove" onClick={removeCartToAPI}>- Remove from cart</button> : 
                  <button className="submit-add"  onClick={postCartToAPI}>+ Add to cart</button>
                }
              </div>
            </div>
          </div>
        </div>
      </div>
    </Fragment>
  )
}

const mapStateToProps = state => {
  return {
    post: state.detailPost,
    user: state.user,
    cart: state.cart
  }
}

const mapDispatchToProps = dispatch => {
  return {
    hideDetail: () => dispatch({type: ActionType.HIDE_DETAIL_PRODUCT}),
    getDataCart: (e) => dispatch({ type: ActionType.GET_CART, data: e }),
    toggleShowCart: () => dispatch({ type: ActionType.TOGGLE_CART })
  }
}

export default connect(mapStateToProps, mapDispatchToProps)(DetailProduct);