import React, { Component, Fragment } from 'react';
import './Home.scss';
import NavCilent from '../../components/NavCIlent/NavCilent'
import Cart from '../../components/Cart/Cart';
import { Transition, animated } from 'react-spring/renderprops';
import Product from '../Product/Product';
import { connect } from 'react-redux';
import DetailProduct from '../../components/DetailProduct/DetailProduct';
// import axios from 'axios';
import ActionType from '../../redux/reducer/globalActionType';
import axios from 'axios';

class Home extends Component {

  getDataUserToAPI = () => {
    axios.get(`http://localhost/server-react-crud-1/get/get-user.php`)
    .then(res => {
      this.props.getDataUser(res.data)
      this.getDataCartToAPI(res.data.id_user)
    })
  }

  getDataCartToAPI = (e) => {
    axios.get(`http://localhost/server-react-crud-1/get/get-cart.php?id=${e}`)
    .then(res => {
      this.props.getDataCart(res.data)
    })
  }

  componentDidMount() {
    this.getDataUserToAPI();
  }

  render() {
    return (
      <Fragment>
        <NavCilent />
        <div className="home-cilent">
          {/* Cart Toggle */}
          <Transition
            native
            items={this.props.showCart}
            from={{
              width: 260,
              height: 100 + 'vh',
              position: 'fixed',
              top: 0,
              right: -270
            }}
            enter={{
              zIndex: 10,
              width: 260,
              height: 100 + 'vh',
              position: 'fixed',
              top: 0,
              right: 0
            }}
            leave={{
              width: 260,
              height: 100 + 'vh',
              position: 'fixed',
              top: 0,
              right: -270
            }}
          >
            {show => show && (props => (
              <animated.div style={props}>
                <Cart />
              </animated.div>
            ))}
          </Transition>
          {/* View Product */}
          <Transition
            native
            items={this.props.showDetailProduct}
            from={{
              // opacity: 0,
              width: 100 + 'vw',
              height: 50 + 'vh',
              position: 'fixed',
              left: 0,
              bottom: -350
            }}
            enter={{
              zIndex: 5,
              // opacity: 1,
              width: 100 + 'vw',
              height: 50 + 'vh',
              position: 'fixed',
              left: 0,
              bottom: 0
            }}
            leave={{
              // opacity: 0,
              width: 100 + 'vw',
              height: 50 + 'vh',
              position: 'fixed',
              left: 0,
              bottom: -350
            }}
          >
            {show => show && (props => (
              <animated.div style={props}>
                <DetailProduct />
              </animated.div>
            ))}
          </Transition>
          <main>
            <div className="main-con">
              <Product />
            </div>
          </main>
        </div>
      </Fragment>
    )
  }
}

const mapStateToProps = state => {
  return {
    showDetailProduct: state.showDetailProduct,
    showCart: state.toggleCart,
    cart: state.cart
  }
}

const mapDispatchToProps = dispatch => {
  return {
    getDataUser: (e) => dispatch({ type: ActionType.GET_DATA_USER, data: e }),
    getDataCart: (e) => dispatch({ type: ActionType.GET_CART, data: e })
  }
}

export default connect(mapStateToProps, mapDispatchToProps)(Home);