import React, { Fragment } from 'react';
import { connect } from 'react-redux';
// import Dummy from '../../assets/img.jpg';
import ActionType from '../../redux/reducer/globalActionType';
// import Cart from '../Cart/Cart';
import './CartList.scss';

const numberSeparator = (x) => {
  return x.toString().replace(/\B(?=(\d{3})+(?!\d))/g, ".");
}

const CartList = (props) => {
  return (
    <Fragment>
      <div className="cart-list-con" onClick={() => props.showDetail(props.cart)}>
        <div className="list-item-img">
          <img src={`http://localhost/server-react-crud-1/files/${props.cart.img}`} alt="IMG"/>
        </div>
        <div className="list-item">
          <div className="item-name">
            Yamaha {props.cart.nama}
          </div>
          <div className="item-detail">
            <div className="item-weight">
              1 Item ({props.cart.berat}kg)
            </div>
            <div className="item-price">
              Rp. {numberSeparator(props.cart.harga_real)}
            </div>
          </div>
        </div>
      </div>
    </Fragment>
  )
}

const mapDispatchToProps = dispatch => {
  return {
    showDetail: (e) => dispatch({type: ActionType.SHOW_DETAIL_PRODUCT, data: e})
  }
}

export default connect(null, mapDispatchToProps)(CartList);