import React, { Fragment } from 'react';
import { connect } from 'react-redux';
import ActionType from '../../redux/reducer/globalActionType';
import CartList from '../CartList/CartList';
import './Cart.scss';

const Cart = (props) => {
  return (
    <Fragment>
      <div className="cart-con">
        <div className="cart-head">
          <div className="head-text">
            <i className="fa fa-shopping-cart fa-fw"></i>
            <span className="cart-text">Cart</span>
          </div>
          <div className="head-close">
            <button className='close-cart' onClick={props.toggleShowCart}>
              <i class="fa fa-caret-right" aria-hidden="true"></i>
            </button>
          </div>
        </div>
        <div className="cart-body">
          {props.cart ? 
            props.cart.map(cart => {
              return <CartList key={cart.id} cart={cart} />
            }) : <p>Fill your bucket</p>}
        </div>
        <div className="cart-foot">
          {props.cart ? <a>Go to cart page</a> : null}  
        </div>
      </div>
    </Fragment>
  )
}

const mapStateToProps = state => {
  return {
    cart: state.cart
  }
}

const mapDispatchToProps = dispatch => {
  return {
    toggleShowCart: () => dispatch({ type: ActionType.TOGGLE_CART })
  }
}

export default connect(mapStateToProps, mapDispatchToProps)(Cart);